@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@800&family=Lora&family=Open+Sans:wght@300&family=Overpass&display=swap);
.App {
  font-family: "Open Sans", sans-serif;
  padding: 1.5rem;
}

.btn {
  display: block;
  cursor: pointer;
  position: relative;
  width: auto;
  color: black;
  border: none;
  border-bottom: 1px white solid;
  border-radius: 3px;
  padding: 0.5rem 0.5rem;
  background-color: rgba(0, 0, 0, 0);
  transition: all 0.2s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.btn::after {
  content: "";
  width: 0%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  border-bottom: 1px black solid;
  transition: all 0.5s;
}

.btn:hover {
  font-size: 1.2rem;
  padding: 0.3rem 0.2rem;
}

.btn:hover::after {
  left: 0;
  width: 100%;
}

.btn:active {
  margin-top: 5px;
  margin-bottom: -5px;
}

.btn:disabled {
  cursor: auto;
  font-size: 1rem;
  padding: 0.5rem 0.5rem;
  border-color: white;
  color: grey;
  background-color: transparent;
}

.btn:disabled::after {
  display: none;
}

.App-main {
  display: grid;
  grid-template-columns: 1fr 1.618fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.App-main:nth-child(1) {
  grid-area: 1 / 1 / 2 / 2;
}
.App-main:nth-child(2) {
  grid-area: 1 / 2 / 2 / 3;
}

.console {
  border: 1px solid black;
  border-radius: 5px;
  height: 50px;
  margin-bottom: 1rem;
}

.console > div:first {
  overflow-y: scroll;
  margin-right: -16px;
  padding-right: 16px;
}

.with-input {
  --console-padding: 2;
  position: relative;
  padding-bottom: 2rem;
}

.with-input > .console-input {
  position: absolute;
  bottom: 0;
  height: 2rem;
  width: 100%;
  padding: var(--console-padding);
  box-sizing: content-box;
  border: none;
}

.console-input > input {
  position: relative;
  border: none;
  border-top: 1px solid black;
  outline: none;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
}

.panel {
  padding: 0;
  list-style-type: none;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.program-field {
  padding: 1rem;
  border: 1px black solid;
  height: 100px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-content: flex-start;
}

.program-field > span {
  color: black;
  font-size: 1rem;
  transition: 0.4s all;
}

.program-field > .highlighted {
  color: red;
  font-size: 1.4rem;
}

body {
  font-family: monospace;
  margin: 0;
  overflow-x: hidden;
}

